<template>
	<b-modal id="ProfileInfoModal" dialog-class="full_modal no_static_height" hide-footer hide-header centered size="lg" @hidden="reset" @shown="shown">
		<template #default>
			<div class="header d-flex justify-content-between align-items-center">
				<span class="fs-lg">{{ $t("editProfileInfo") }}</span>
				<BIcon icon="x" @click="reset" scale="0.8" class="btn_icon btn_icon_md text-muted" />
			</div>
			<b-form @submit.prevent="save">
				<div class="p-2 py-4 my-2">
					<b-row>
						<b-col cols="12" sm="6" class="mb-3">
							<FormInput v-model="profile.firstName" v-vuelidate.noDirty="profile.firstName" :label="$t('firstName')" required />
						</b-col>
						<b-col cols="12" sm="6" class="mb-3">
							<FormInput v-model="profile.lastName" v-vuelidate.noDirty="profile.lastName" :label="$t('lastName')" required />
						</b-col>
						<b-col cols="12">
							<FormInput v-model="profile.username" v-vuelidate.noDirty="profile.username" :label="$t('username')" required />
						</b-col>
					</b-row>
				</div>
				<div class="footer d-flex align-items-center justify-content-end">
					<b-btn class="filter_btn active mx-3" size="sm" @click="reset">{{ $t("cancel") }}</b-btn>

					<b-overlay :show="isLoading" rounded opacity="0.6" spinner-small spinner-variant="primary" class="d-inline-block">
						<b-btn type="submit" variant="primary" :disabled="$v.$invalid || isLoading">{{ $t("save") }}</b-btn>
					</b-overlay>
				</div>
			</b-form>
		</template>
	</b-modal>
</template>

<script>
	import FormInput from "@/components/inputs/FormInput";

	import { validationMixin } from "vuelidate";

	import { required } from "vuelidate/lib/validators";

	let invalid = (v) => /(^[a-zA-Z0-9]+$)|(^[a-zA-Z0-9]+_[a-zA-Z0-9]+$)/.test(v);

	export default {
		components: { FormInput },

		mixins: [validationMixin],

		data() {
			return {
				isLoading: false,
				profile: { username: this.$me?.username, firstName: this.$me?.firstName, lastName: this.$me?.lastName },
			};
		},

		validations: {
			profile: {
				username: { required, invalid },
				firstName: { required, invalid },
				lastName: { required, invalid },
			},
		},

		methods: {
			async save() {
				if (this.$v.$invalid) return;

				this.isLoading = true;

				try {
					await this.$store.dispatch("updateMe", this.profile);
				} finally {
					this.isLoading = false;
				}

				this.$bvModal.hide("ProfileInfoModal");
			},

			shown() {
				this.profile = {
					username: this.$me.username,
					firstName: this.$me.firstName,
					lastName: this.$me.lastName,
				};
			},

			reset() {
				this.$bvModal.hide("ProfileInfoModal");
			},
		},
	};
</script>

<style></style>
