<template>
	<account activeTab="general">
		<section>
			<div class="field_title pt-1 d-flex align-items-center">
				<BIcon icon="person-badge" class="mr-2" />
				<div class="title font-weight-500">{{ $t("personalInfo") }}</div>
			</div>
			<div class="d-flex align-items-center mt-3">
				<div class="user_image_wrapper overflow-hidden mr-2 d-flex align-items-center justify-content-center position-relative rounded-circle">
					<label for="userImage" class="user_image_label position-absolute">
						<CameraPlus width="1.7rem" />
					</label>

					<b-form-file id="userImage" hidden class="d-none" @input="uploadProfileImage" />

					<template v-if="profileImage">
						<b-avatar size="4rem" variant="primary" :src="profileImage" rel="noreferrer" />
					</template>
					<b-avatar size="4rem" variant="primary" v-else />
				</div>
				<div class="ml-2">
					<h5 class="font-weight-normal d-flex align-items-center">
						<span>{{ $me.username }}</span>
						<div class="d-flex align-items-center fs-md text-primary font-weight-500 ml-4 cursor-pointer" v-b-modal.ProfileInfoModal>
							<b-icon icon="pencil" class="mr-2" width="1em"></b-icon>
							<span>{{ $t("edit") }}</span>
						</div>
					</h5>
					<span class="text-muted">@{{ $me.username }}</span>
				</div>
			</div>
		</section>
		<section class="my-5">
			<div class="field_title pt-1 d-flex align-items-center mb-2">
				<BIcon icon="shield-lock" class="mr-2" />
				<div class="title font-weight-500">{{ $t("security") }}</div>
			</div>
			<p>{{ $t("securityDesc", { email: $me.email }) }}</p>
		</section>
		<section class="my-5">
			<div class="field_title pt-1 d-flex align-items-center mb-2">
				<BIcon icon="exclamation-triangle" class="mr-2" />
				<div class="title font-weight-500">{{ $t("dangerZone") }}</div>
			</div>
			<b-btn variant="outline-danger" class="mt-2">{{ $t("deleteAccount") }}</b-btn>
		</section>
		<ProfileInfoModal />
	</account>
</template>

<script>
	import Account from "@/components/Account";
	import ProfileInfoModal from "@/components/modals/ProfileInfoModal";
	import CameraPlus from "@/components/icons/cameraPlus";
	export default {
		components: { Account, ProfileInfoModal, CameraPlus },

		data() {
			return {};
		},

		computed: {
			profileImage() {
				let picture = this.$me.profile?.picture;

				if (!picture) return "";

				if (/https:\/[A-z0-9]/.test(picture)) {
					picture = "https://" + picture.slice(7);
				}

				return picture;
			},
		},

		methods: {
			uploadProfileImage(file) {
				if (!file) return;

				let extentions = ["image/png", "image/jpg", "image/jpeg"];

				if (!extentions.includes(file.type)) {
					return alert("image type must be one of this types 'png,jpg,jpeg'");
				}

				let size = 5 * 1024 * 1024;

				if (file.size > size) {
					return alert("image size is too long");
				}

				let data = [{ image: file }];

				if (file) {
					let formData = new FormData();

					formData.set("image", file);

					data = [formData, { "Content-Type": "multipart/form-data" }];
				}

				this.$store.dispatch("updateProfile", data);
			},
		},
	};
</script>

<style lang="scss">
	.user_image_wrapper {
		.user_image_label {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 110%;
			height: 110%;
			background-color: rgba(0, 0, 0, 0.4);
			z-index: 1;
			opacity: 0;
			transition: 0.2s;
			cursor: pointer;
			svg {
				fill: #fff;
			}
		}
		&:hover {
			.user_image_label {
				opacity: 1;
			}
		}
	}
</style>
